@import 'base'; // this line imports the file src/_base.scss
@import 'add-two';
@import 'colorize-red';

@mixin circle {
  width: 200px;
  height: 200px;
  border-radius: 50%;
}

.grey {
  @include circle;
  background: colorize-red(lightgray, 255);
}
.green {
  @include circle;
  background: colorize-red(seagreen, 255);
}
.blue {
  @include circle;
  background: colorize-red(blue, 255);
}